import React, {useContext, useEffect} from 'react'
import Header from '../Components/Header'
import Card from '../Components/Card'
import GlobalStateContext from '../Global/GlobalStateContext'
import styled from 'styled-components'
import Pokedex from './Pokedex'

const Content = styled.div `
    display: flex;
    justify-content: space-around;
    flex-flow: wrap;
`

export default function Home() {

        
    const {list, setList, pokedex, setPokedex,offset, setOffset} = useContext(GlobalStateContext)

    const sortedArray = list.sort((a, b) => { return a.data.id - b.data.id})
    console.log(list)


    const nextPage = () => {
        if (offset <= 1000) {
            setOffset(offset+20)
        }
    }
    const lastPage = () => {
        if(offset > 0) {
            setOffset(offset-20)
        }
    }

const checkPokemon = sortedArray.map((e, index) => {
                   
    if(e.data.id >offset && e.data.id <=offset+20) {
     return <Card 
                 id={e.data.id}
                 key={e.data.id}
                 name={e.data.name}
                 img={e.data.sprites.front_default}
                 pokemon={e}
                 buttonText={"Adicionar à Pokédex"}
             /> 
     }  
    })

    return (
        <div>
            <Header 
                routeButton={'Abrir Pokédex'}
                path={'/pokedex'}
                name={'Lista de Pokemons'}
            />
            <p>Página {offset/20+1}</p>
            <button onClick={lastPage}>menos</button>
            <button onClick={nextPage}>mais</button>
            <Content>
                
                {sortedArray[0] ? sortedArray.map((e, index) => {
                   //passar esta checkagem para antes de ordenar o array para usar a condicional para atualizar a página
                   if(e.data.id >offset && e.data.id <=offset+20) {
                    return <Card 
                                id={e.data.id}
                                key={e.data.id}
                                name={e.data.name}
                                img={e.data.sprites.front_default}
                                pokemon={e}
                                buttonText={"Adicionar à Pokédex"}
                            /> 
                    }  
                   }) : <div>Loading...</div>}
            </Content>
        </div>
    )
}
